// 公共参数
import { TParams } from '@locallife/base-request';

export interface CommonRequestParams {
    params?: CommonParams;
    headers: CommonHeaders;
    queryParams?: TParams;
    businessName?: string;
    responseType?: string;
    timeout?: number; // 请求超时，单位 s
}

export interface CommonParams {
    bundleVersionCode?: number;
    extend?: string;
    channelPage?: string;
    channelSource?: string;
    [propName: string]: string | number | boolean | undefined;
}

export interface CommonHeaders {
    contentType?: string;
    accept?: string;
    clientType?: number;
    [propName: string]: string | number | boolean | undefined;
}
